import { Node,  SettingType} from '../../node';
import { Container } from '../../container';
import { Type } from '../../node-io';
import { NODE_INFO } from '../../node-info';
import Utils from '../../utils';
import TimeUtils, { TIME_TYPE }  from '../../utils/time-utils';

class PACUnitControl extends Node {
  //Inputs
  ENABLESLOT = 0;
  ZTSLOT = 1;
  SPSLOT = 2;
  CLGOFSTSLOT = 3;
  HTGOFSTSLOT = 4;
  STGUPSLOT = 5;
  MDLYSLOT = 6;
  ECONOASLOT = 7;
  OATSLOT = 8;
  ECONHISLOT = 9;
  ECONLOSLOT = 10;
  FSSLOT = 11;

  //Outputs
  CLGMSLOT = 0;
  HTGMSLOT = 1;
  COMPSTGSLOT = 2;
  ECONOMSLOT = 3;
  OADMPSLOT = 4;
  RVSLOT = 5
  COMPSTARTSLOT = 6;

  constructor() {
    super();
    this.title = NODE_INFO.hvac['pac-unit-control'].title;
    this.description = NODE_INFO.hvac['pac-unit-control'].description; 
   
    this.addInputWithSettings('enable', Type.BOOLEAN, true, 'Enable');
    this.addInput('zoneTemp', Type.NUMBER);
    this.addInputWithSettings('setpoint', Type.NUMBER, 20, 'Setpoint');
    this.settings['straddleSP'] = {
      description: 'Setpoint Logic',
      value: true,
      type: SettingType.DROPDOWN,
      config: {
        items: [
          { value: true, text: 'Condition to Setpoint' },
          { value: false, text: 'Condition past Setpoint' },
        ],
      },
    };
    this.addInputWithSettings('CLG offset', Type.NUMBER, 1, 'CLG offset');
    this.addInputWithSettings('HTG offset', Type.NUMBER, 1, 'HTG offset');
    this.addInputWithSettings('stageUpDelay (minutes)', Type.NUMBER, 10, 'Stage Up Delay (Minutes)');
    this.addInputWithSettings('modeDelay (minutes)', Type.NUMBER, 15, 'Mode Change Delay (Minutes)');
    this.addInputWithSettings('econoAllow', Type.BOOLEAN, true, 'Allow Economy Mode');
    this.addInput('OATemp', Type.NUMBER);
    this.settings['compStages'] = {
      description: '# Compressor Stages',
      value: 2,
      type: SettingType.NUMBER,
    };
    this.addInputWithSettings('econoHigh', Type.NUMBER, 22, 'Econo OA High Temp');
    this.addInputWithSettings('econoLow', Type.NUMBER, 10, 'Econo OA Low Temp');
    this.settings['econoDB'] = {
      description: 'Economy Conditions Deadband',
      value: 1,
      type: SettingType.NUMBER,
    };
    this.settings['requireFS'] = {
      description: 'Require Fan Status?',
      value: true,
      type: SettingType.BOOLEAN,
    };
    this.settings['FSdelay'] = {
      description: 'Fan Status OFF Delay (Seconds)',
      value: 20,
      type: SettingType.NUMBER,
    };
    this.addInput('fanStatus', Type.BOOLEAN);

    this.addOutput('CLG Mode', Type.BOOLEAN);
    this.addOutput('HTG Mode', Type.BOOLEAN);
    this.addOutput('compStage', Type.NUMBER);
    this.addOutput('econoMode', Type.BOOLEAN);
    this.addOutput('OADamper', Type.NUMBER);
    this.addOutput('RV', Type.BOOLEAN);
    this.addOutput('Comp1', Type.BOOLEAN);
    this.addOutput('Comp2', Type.BOOLEAN);

    this.setSettingsConfig({
      groups: [
        { 'CLG offset': {}, 'HTG offset': {} },
        { 'stageUpDelay (minutes)': {}, 'modeDelay (minutes)': {} },
        { econoLow: {}, econoHigh: {} },
      ],
      conditions: {},
    });
  }

  onCreated() {
    this.EXECUTE_INTERVAL = 10000;
    this.properties['numComps'] = 2;
    this.properties['FSInput'] = true;
    this.properties['fanStatus'] = false;
    this.properties['fanStatusOffTime'] = null;
    this.properties['clgModeEnd'] = null;
    this.properties['htgModeEnd'] = null;
    this.resetController();
    this.onAfterSettingsChange();
  }

  onAdded() {
    this.EXECUTE_INTERVAL = 10000;
    this.setOutputData(0, this.properties['clgMode']);
    this.setOutputData(1, this.properties['htgMode']);
    this.setOutputData(2, this.properties['compStage']);
    this.setOutputData(3, this.properties['econoConditions']);
    this.onAfterSettingsChange();
  }

  onExecute(){
    this.onInputUpdated();
  }

  onAfterSettingsChange() {
    this.updateStageCount();
    this.updateFSInput();
    this.onInputUpdated();
  }

  onInputUpdated() {
    const enable = this.getInputData(this.ENABLESLOT);
    var fanStatus = true;
    if (this.settings['requireFS'].value){ 
      const fanStatusInput = this.getInputData(this.FSSLOT);
      if (fanStatusInput) {
        fanStatus = true;
        this.properties['fanStatus'] = true;
      }
      else {
        if (this.properties['fanStatus']) {
          this.properties['fanStatusOffTime'] = Date.now() + TimeUtils.timeConvert(this.settings['FSdelay'].value, TIME_TYPE.SECONDS);
        }
        this.properties['fanStatus'] = fanStatusInput;
        if (this.properties['fanStatusOffTime'] <= Date.now()) {
          fanStatus = false;
        }
      }
    } 

    if (!enable || (this.settings['requireFS'].value && !fanStatus)) {
      this.resetController();
      return;
    }
    const input = this.getInputData(this.ZTSLOT);
    if (input == null || input == undefined) {
      this.resetController();
      return;
    }
 
    const setpoint = this.getInputData(this.SPSLOT);
    const clgOffset = this.getInputData(this.CLGOFSTSLOT);
    const htgOffset = this.getInputData(this.HTGOFSTSLOT);
    const clgSP = setpoint + clgOffset;
    const htgSP = setpoint - htgOffset;

    if (!this.properties['clgMode'] && !this.properties['htgMode'] && input < htgSP) {
      if (this.properties['clgModeEnd'] == null || (this.properties['clgModeEnd'] + TimeUtils.timeConvert(this.getInputData(this.MDLYSLOT), TIME_TYPE.MINUTES)) <= Date.now()){
        this.properties['htgMode'] = true;
        this.properties['compStage'] = 0;
      }
    }
    else if (!this.properties['htgMode'] && !this.properties['clgMode'] && input > clgSP) {
      if (this.properties['htgModeEnd'] == null || (this.properties['htgModeEnd'] + TimeUtils.timeConvert(this.getInputData(this.MDLYSLOT), TIME_TYPE.MINUTES)) <= Date.now()){
        this.properties['clgMode'] = true;
        this.properties['compStage'] = 0;
      }
    }

    if (this.properties['htgMode']){
      this.properties['econoConditions'] = false;
      if (input < htgSP) {
        if(this.properties['compStage'] === 0){
          this.properties['compStage'] = 1;
          this.properties['stageStartTime'] = Date.now();
        }
        else if (this.properties['compStage'] > 0 && (this.properties['stageStartTime'] + TimeUtils.timeConvert(this.getInputData(this.STGUPSLOT), TIME_TYPE.MINUTES)) <= Date.now()){
          if (this.properties['compStage'] < this.properties['numComps']) {
            this.properties['compStage']++;
            this.properties['stageStartTime'] = Date.now();
          }
        }
      }
      else {
        var offSPInterval = htgOffset / this.properties['numComps'];
        if (this.settings['straddleSP'].value){
          for (var s = this.properties['compStage']; s > 0; s--) {
            if (input >= (setpoint - (offSPInterval * (s-1)))) {
              this.properties['compStage']--;
              this.properties['stageStartTime'] = Date.now();
            }
          }
        }
        else if (!this.settings['straddleSP'].value){
          offSPInterval = (htgOffset*2) / this.properties['numComps'];
          for (var s = this.properties['compStage']; s > 0; s--) {
            if (input >= ((setpoint + htgOffset) - (offSPInterval * (s-1)))) {
              this.properties['compStage']--;
              this.properties['stageStartTime'] = Date.now();
            }
          }
        }
        if (this.properties['compStage'] === 0){
          this.properties['htgMode'] = false;
          this.properties['htgModeEnd'] = Date.now();
        }
      }
    }
    else if (this.properties['clgMode']){
      if (input > clgSP) {
        if(this.properties['compStage'] === 0){
          this.properties['compStage'] = 1;
          this.properties['stageStartTime'] = Date.now();
        }
        else if (this.properties['compStage'] > 0 && (this.properties['stageStartTime'] + TimeUtils.timeConvert(this.getInputData(this.STGUPSLOT), TIME_TYPE.MINUTES)) <= Date.now()){
          if (this.properties['compStage'] < this.properties['numComps']) {
            this.properties['compStage']++;
            this.properties['stageStartTime'] = Date.now();
          }
        }
      }
      else {
        var offSPInterval = clgOffset / this.properties['numComps'];
        if (this.settings['straddleSP'].value){
          for (var s = this.properties['compStage']; s > 0; s--) {
            if (input <= (setpoint + (offSPInterval * (s-1)))) {
              this.properties['compStage']--;
              this.properties['stageStartTime'] = Date.now();
            }
          }
        }
        else if (!this.settings['straddleSP'].value){
          offSPInterval = (clgOffset*2) / this.properties['numComps'];
          for (var s = this.properties['compStage']; s > 0; s--) {
            if (input <= ((setpoint - clgOffset) + (offSPInterval * (s-1)))) {
              this.properties['compStage']--;
              this.properties['stageStartTime'] = Date.now();
            }
          }
        }
        if (this.properties['compStage'] === 0){
          this.properties['clgMode'] = false;
          this.properties['clgModeEnd'] = Date.now();
        }
      }

      if (this.getInputData(this.ECONOASLOT)){
        const OATemp = this.getInputData(this.OATSLOT);
        if (this.checkEconoConditions(OATemp)) this.properties['econoConditions'] = true;
        else this.properties['econoConditions'] = false;
      }
      else this.properties['econoConditions'] = false;
    }

    this.setOutputs(input, setpoint, clgSP);
  }

  setOutputs(temp:number, setpoint:number, clgSP:number) {
    this.setOutputData(this.CLGMSLOT, this.properties['clgMode']);
    this.setOutputData(this.HTGMSLOT, this.properties['htgMode']);
    this.setOutputData(this.COMPSTGSLOT, this.properties['compStage']);
    this.setOutputData(this.ECONOMSLOT, this.properties['econoConditions']);
    if (this.properties['econoConditions'] && this.properties['clgMode']) this.setOutputData(this.OADMPSLOT, Utils.remap(temp, setpoint, clgSP, 0, 100));
    else this.setOutputData(this.OADMPSLOT, 0);
    this.setOutputData(this.RVSLOT, this.properties['htgMode']);
    for(var x = 0; x < this.properties['numComps']; x++){if (this.properties['compStage'] >= (x+1)) this.setOutputData(x + this.COMPSTARTSLOT, true);
      if (this.properties['compStage'] >= (x+1)) this.setOutputData(x + this.COMPSTARTSLOT, true);
      else this.setOutputData(x + this.COMPSTARTSLOT, false);
    }
  }

  checkEconoConditions(OATemp:number) {
    const econoHigh = this.getInputData(this.ECONHISLOT);
    const econoLow = this.getInputData(this.ECONLOSLOT);
    const econoDB = this.settings['econoDB'].value;
    if (econoHigh <= (econoLow + econoDB) || econoHigh == null || econoLow == null || econoDB == null) {
      this.properties['econoConditions'] = false;
      return false;
    }
    if (OATemp < econoLow || OATemp > econoHigh) {
      this.properties['econoConditions'] = false;
      return false;
    }
    if (OATemp <= (econoHigh - econoDB) && OATemp >= (econoLow + econoDB)) {
      this.properties['econoConditions'] = true;
    }
    return this.properties['econoConditions'];  //return current Econo state, or updated.
  }

  updateFSInput() {
    if(this.settings['requireFS'].value != this.properties['FSInput']){
      if(this.settings['requireFS'].value) this.addInput('fanStatus', Type.BOOLEAN);
      else this.removeInput(11);
    }
    this.properties['FSInput'] = this.settings['requireFS'].value;
  }

  updateStageCount() {
    const compSettings = Math.max(1, this.settings['compStages'].value);
    if (this.settings['compStages'].value < 1) this.settings['compStages'].value = 1;
    const diff = this.settings['compStages'].value - this.properties['numComps'];
    if (diff == 0) return;
    //add
    let num = null;
    if (diff > 0) for (let i = 0; i < diff; i++) {
      num = this.addOutput('Comp'+(this.properties['numComps'] + i + 1), Type.BOOLEAN);
      this.setOutputData(num, false);
    }
    //remove
    if (diff < 0) {
      let ids = Object.keys(this.outputs);
      for (let i = 0; i > diff; i--) {
        let id = ids[ids.length + i - 1];
        this.removeOutput(+id);
      }
    }
    this.properties['numComps'] = this.settings['compStages'].value;
    if (this.properties['compStage'] > this.properties['numComps']) this.properties['compStage'] = this.properties['numComps'];
  }

  resetController(){
    this.properties['clgMode'] = false;
    this.properties['htgMode'] = false;
    this.properties['compStage'] = 0;
    this.properties['econoMode'] = false;
    this.properties['econoConditions'] = false;
    this.properties['stageStartTime'] = null;
    this.setOutputData(this.CLGMSLOT, this.properties['clgMode']);
    this.setOutputData(this.HTGMSLOT, this.properties['htgMode']);
    this.setOutputData(this.COMPSTGSLOT, this.properties['compStage']);
    this.setOutputData(this.ECONOMSLOT, this.properties['econoMode']);
    this.setOutputData(this.OADMPSLOT, 0);
    this.setOutputData(this.RVSLOT, false);
    for(var x = 0; x < this.properties['numComps']; x++){
      this.setOutputData(x + this.COMPSTARTSLOT, false);
    }
  }

}
Container.registerNodeType('hvac/pac-unit-control', PACUnitControl);

