import { Node } from './node';
import { DispatchNode } from '../pub-sub/DispatchNode';

export default class Registry {
  static _nodes: { [id: string]: Node } = {};
  static _dispatchNodes: { [id: string]: DispatchNode } = {};

  static getId(containerId: number, nodeId: number) {
    return `c${containerId}n${nodeId}`;
  }
}
